<template>
    <div>
        <p class="navigation "><em>Home</em> > 
        <router-link class="design-link" to="/"><em>Dashboard</em></router-link>
         > 
        <router-link class="design-link" to="/events"><em>Events</em></router-link></p>
    </div>
    <br><br>
    <div class="content"></div>
    <div class="search-elt ">
       <input type="text" v-model="name" class="search" id="eventName" placeholder="Search by event name...">
       <button @click="searchName"  class="find">
        <span  class="material-icons search-icon">search</span>
       </button>
    </div>

    <br>

    <div class="d-flex align-items-center justify-content-center">

            <div class="view" :class="elementFound ? '' : 'hide'">
                <h3>Event name: {{name}}</h3>
                <ul class="event-prop">
                    <li class="collection-item">Event ID#: {{id}}</li>
                    <li class="collection-item">Event description: {{description}}</li>
                    <li class="collection-item">Event type: {{type}}</li>
                    <li class="collection-item">Event priority: {{priority}}</li>
                    <li class="collection-item">Related events: {{relatedevents}}</li> 
                </ul>
                
                <router-link class="create-event" to="/events">
                    <button class="btn back">Back</button>
                </router-link>

                <button @click="modifyButton" class="btn modify-btn">Modify</button>
                <button @click="deleteEvent" class="btn delete">Delete</button>
            </div>
            
            <div :class="modifyElement ? '' : 'hide'" class="form-modify ">
                <h4>Event modification: {{name}}</h4>
                <form @submit.prevent="updateEvent">
                    
                    <label class="col-sm-3" for="eventId">Event ID#:</label>
                    <input class="col-sm-8" id="eventId" required type="text" v-model="id" placeholder="Event ID#:"><br>
                    
                    <label class="col-sm-3" for="description">Event description:</label>
                    <input class="col-sm-8" id="description" required type="text" v-model="description" placeholder="Event description:"><br>
                    
                    <label class="col-sm-3" for="type">Event type:</label>
                    <select required id="type" v-model="type" class="col-sm-8" >
                            <option value="crosspromo" selected>crosspromo</option>
                            <option value="liveops">liveops</option>
                            <option value="app">app</option>
                            <option value="ads">ads</option>
                    </select><br>
                    
                    <label class="col-sm-3" for="priority">Event priority:</label>
                    <input class="col-sm-8" id="priority" required type="number" max="10" min="0" v-model="priority"  placeholder="Event priority:"><br>
                    
                    <label class="col-sm-3" for="relatedevents">Related events:</label>
                    <input class="col-sm-8" id="relatedevents" type="text" v-model="relatedevents"  placeholder="Related events:"><br> 
                    
                    <div>
                      <router-link class="create-event" to="/events">
                        <button class="btn back">Back</button>
                    </router-link>

                    <button type="submit" class="btn save">Save</button>  
                    </div>
                    
                </form>
                
            </div>
    </div>
</template>

<script>
import { doc, getDoc, setDoc, deleteDoc, collection, getDocs } from 'firebase/firestore';
import { db } from '@/firebase';

    export default{
        name: 'Event-modification',
        data() {
            return{
                id: '',
                name: '',
                description: '',
                type: '',
                priority: '',
                relatedevents: ' ',   
                elementFound: false,
                modifyElement: false        
            }
        },
        mounted(){
            this.elementFound = false
        },
        methods: {
            async searchName() {
                this.modifyElement = false

                const docRef = doc(db, "events", this.name)
                const docSnap = await getDoc(docRef)

                if (docSnap.exists()) {
                console.log("Document data:", docSnap.data())
                    this.id = docSnap.data().id
                    this.name = docSnap.data().name
                    this.description = docSnap.data().description
                    this.type = docSnap.data().type
                    this.priority = docSnap.data().priority
                    this.relatedevents = docSnap.data().relatedevents

                this.elementFound = true

                } else {
                // doc.data() will be undefined in this case
                alert("There is no such event! Please try again")
                }
  
            },
            modifyButton() {
                this.modifyElement = true
                this.elementFound = false
            },
            async updateEvent() {

                const querySnapshot = await getDocs(collection(db, "events"));
                let count = 0
                    querySnapshot.forEach((doc) => {
                    if (doc.data().id==this.id){
                        count++
                    }
                    });
        
            if (count<1){
                if (confirm("Are you sure?!")){
                   await setDoc(doc(db, "events", this.name), {
                    id: this.id,
                    name: this.name,
                    description: this.description,
                    type: this.type,
                    priority: this.priority,
                    relatedevents: this.relatedevents,  
                    });
                    this.modifyElement = false
                    this.elementFound = false 
                }     
            }else {
                alert("Unfortunatelly, an event with that id exists. Please try again with another id.")
            }

                            
            },
            async deleteEvent() {
                if (confirm("Are you sure you want to delete the event?!")){
                    await deleteDoc(doc(db, "events", this.name))
                    this.elementFound = false
                }
            }
        }
        
    }

</script>

<style scoped>
    .navigation{
        color: darkgrey;
        display: block;
        float: left;
        margin-left: 90px;
    }
    em:hover{
        color: black;
        cursor: pointer;
    }
    .design-link {
        color: darkgrey;
        text-decoration: none;
    }
    .search-elt{
        display: flex;
        flex-direction: row;
        margin-left: 100px;
        margin-top: 30px;
    }
    .modify{
        display: none;
    } 
    .view{
        margin: 0;
    }
    .btn {
        margin: 5px;
        border: 1px solid darkgray;
        box-shadow: 2px 2px 5px rgb(118, 133, 127);
    }
    .back:hover {
        background-color: rgb(117, 112, 112);
        transition: 0.2s ease-out;
        color: white;
    }
    .delete:hover{
        background-color: red;
        transition: 0.2s ease-out;
        color: white;
    }
    .modify-btn:hover{
        background-color: rgb(35, 116, 94);
        transition: 0.2s ease-out;
        color: white;
    }
    .save:hover {
        background-color: rgb(91, 221, 82);
        transition: 0.2s ease-out;
        color: white;
    }
    .view{
        width: 70%;
        padding: 20px;
        border: 2px solid rgb(133, 130, 130);
    }
    li{
        margin: 3px;
        border: 1px solid rgb(187, 186, 186);
        text-align: left;
        padding-left: 5px;
    }
    .find{
        border: none;
        background-color: rgb(133, 130, 130);
        box-shadow: 2px 2px 5px rgb(108, 105, 105);
    }
    .hide {
        display: none;
    }
    .form-modify{
        margin: 20px;
        padding: 15px;
        width: 70%;
        
    }
    .col-sm-8{
        margin: 4px;
    }
    select{
        padding: 4px;
    }
    label{
        text-align: right;
    }
    .search{
        border: none;
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
        background-color: rgba(206, 206, 206, 0.392);
        padding-left: 5px;
        box-shadow: 1px 1px 5px grey;
    }
    .search:hover{
        box-shadow: 1px 1px 5px rgb(9, 130, 92);
    }

</style>