<template>
    <!-- <Slidebar /> -->
    <div class="Events-view">
        <p class="navigation "><em>Home</em> > 
        <router-link class="design-link" to="/"><em>Dashboard</em></router-link>
         > 
        <router-link class="design-link" to="/events"><em>Events</em></router-link>
        </p>

        <router-link class="create-event" to="/view-modify-delete">
            <button class="view-button btn ">View an event</button>
        </router-link>
        <br><br><br>
        <h5>Create an event:</h5>
        <!-- Form for creating the event -->
        <div class="d-flex align-items-center justify-content-center">
           <form @submit.prevent="addEvent">
            <div class="col">

                <!-- id -->
                <div class="form-group row">
                    <label class="col-sm-2 col-form-label" for="eventId">Id: </label>
                    <div class="col-sm-10">
                        <input required type="text" v-model="id" class="form-control" id="eventId" placeholder="Event id...">
                    </div>
                </div>

                <!-- name -->
                <div class="form-group row">
                    <label class="col-sm-2 col-form-label" for="eventName">Name: </label>
                    <div class="col-sm-10">
                        <input required type="text" v-model="name" class="form-control" id="eventName" placeholder="Event name...">
                    </div>
                </div>

                <!-- description -->
                <div class="form-group row">
                    <label class="col-sm-2 col-form-label" for="description">Description: </label>
                    <div class="col-sm-10">
                        <input required type="text" v-model="description" class="form-control" id="description" placeholder="Description...">
                    </div>
                </div>

                <!-- type -->
                <div class="form-group row">
                    <label class="col-sm-2 col-form-label" for="type">Type: </label>
                    <div class="col-sm-10">
                        <select required id="type" v-model="type" class="form-select" aria-label="Default select">
                            <option value="crosspromo" selected>crosspromo</option>
                            <option value="liveops">liveops</option>
                            <option value="app">app</option>
                            <option value="ads">ads</option>
                        </select>               
                    </div>
                </div>

                <!-- priority -->
                <div class="form-group row">
                    <label class="col-sm-2 col-form-label" for="priority">Priority: </label>
                    <div class="col-sm-10">
                        <input required type="number" v-model="priority" class="form-control" id="proprity" placeholder="Event priority..." max="10" min="0">
                    </div>
                </div>

                <!-- related-events -->
                <div class="form-group row">
                    <label class="col-sm-2 col-form-label" for="relatedevents">Related Events: </label>
                    <div class="col-sm-10">
                        <input type="text" v-model="relatedevents" class="form-control" id="relatedevents" placeholder="Related events...">
                    </div>
                </div>
            </div>
          
            <button type="submit" class="btn">Add</button>
            </form> 
        </div>
        
        

    </div>
</template>

<script>
import { doc, setDoc, getDocs, collection  } from 'firebase/firestore';
 import { db } from '@/firebase';


    export default {
        name: 'Events-view',
        data() {
            return{
                id: '',
                name: '',
                description: '',
                type: '',
                priority: '',
                relatedevents: '',            
            }
        },
        methods: {
           async addEvent() {
                const querySnapshot = await getDocs(collection(db, "events"));
                let count = 0
                let eventNameExists = 0
                    querySnapshot.forEach((doc) => {
                    if (doc.data().id==this.id){
                        count++
                    }
                    if (doc.id == this.name){
                        eventNameExists++
                    }
                    });

            if (count<1){
                if (eventNameExists<1){
                    await setDoc(doc(db, "events", this.name), {
                    id: this.id,
                    name: this.name,
                    description: this.description,
                    type: this.type,
                    priority: this.priority,
                    relatedevents: this.relatedevents,
                    });
                    alert("The document is successfully created!!!");  
                } else {
                    alert("Unfortunatelly, an event with this name exists... Please choose another name!")
                }  
            } else {
                alert("Unfortunatelly, an event with this id exists... Please choose another id!")
            }       
           } 
        } 
    }
</script>

<style scoped>
    .navigation{
        color: darkgrey;
        display: block;
        float: left;
        margin-left: 90px;
    }
    em:hover{
        color: black;
        cursor: pointer;
    }
    .design-link {
        color: darkgrey;
        text-decoration: none;
    }
    .form-group{
        margin: 10px;
    }
    label{
        margin-right: 10px;
        font-weight: bold
    }
    input:hover, select:hover{
        border-bottom: 2px solid rgb(82, 91, 88);
        box-shadow: 2px 2px 5px rgb(12, 107, 88);
        transition: 0.3s ease-in;
    }
    .btn{
        margin-right: 50px;
        width: 150px;
        background-color: rgb(82, 91, 88);
        color: white;
    }
    .btn:hover{
        background-color: rgb(3, 183, 147);
        box-shadow: 2px 2px 5px rgb(22, 23, 23);
        font-weight: bold;
        transition: 0.3s ease-in;
    }
    h5{
        font-weight: bold;
        color: rgb(87, 87, 87);
        margin-right: 50px;
    }
    form{
        border: 1px solid rgb(107, 115, 112);
        border-radius: 10px;
        padding: 15px;
        margin: 10px;
    }
    .view-button{
        float: right;
        position: relative;
        top: 0;
    }
    input, select {
        border: none;
        border-bottom: 2px solid rgb(82, 91, 88);
        
    }
    
</style>